@import url(https://fonts.googleapis.com/css2?family=Damion&display=swap);
@font-face {
    font-family: ReneLight;
    src: url(/login-demo/static/media/ReneLight.2d813443.otf) format("opentype");
}

@font-face {
    font-family: ReneBold;
    src: url(/login-demo/static/media/ReneBold.4c9042f1.otf) format("opentype");
}

body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
        "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
        "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
        monospace;
}

.App{text-align:center;height:100vh;width:100vw;background-color:#faa916;display:flex;justify-content:center;align-items:center}

.container{border-radius:5px;background-color:#fff4e9;height:800px;width:500px;display:flex;flex-direction:column;justify-content:flex-start;align-items:center}.container .title{position:relative;font-size:3.5em;font-family:"Damion", cursive;top:2%}.container .description{width:100%;height:50px;position:relative;display:flex;justify-content:center;align-items:center;overflow:hidden;font-family:ReneBold;font-size:1.2em}.container .description div{position:absolute;display:flex;justify-content:center;align-items:center;width:70%;height:100%;opacity:0.8}.container .description .description-item2{left:-100%}.container .description .description-item3{left:-200%}.container .sign-up{width:100%;display:flex;flex-direction:column;justify-content:center;align-items:center;margin-top:55px}.container .sign-up .loginButton{display:flex;justify-content:center;align-items:center;opacity:1;width:70%;height:35px;border-radius:50px;box-shadow:inset 0px 0px 0px 0.5px #000000;margin-bottom:10px}.container .sign-up .create{opacity:0.5}.container .navDots{display:flex;justify-content:space-between;width:72px;margin-top:40px}.container .navDot{background:black;border-radius:3px;height:6px;width:6px}.container .people{position:relative;overflow:hidden;width:100%;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.container .people div{position:absolute;height:100%;width:100%;background-position:center;background-size:contain;background-repeat:no-repeat}.container .people .person1{background-image:url(/login-demo/static/media/1stPersonTransparent.b50deae1.png)}.container .people .person2{background-image:url(/login-demo/static/media/2ndPersonTransparent.ea6a7779.png);left:-100%}.container .people .person3{background-image:url(/login-demo/static/media/3rdPersonTransparent.517dc4f5.png);left:-200%}

